/* Hide for mobile, show later */
.sidebar {
    display: none;
  }
  @media (min-width: 768px) {
    .sidebar {
      position: fixed;
      top: 51px;
      bottom: 0;
      left: 0;
      z-index: 1000;
      display: block;
      padding: 20px;
      overflow-x: hidden;
      overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
      background-color: #f5f5f5ec;
      /* border-right: 1px solid #eee; */
    }
  }
  
  /* Sidebar navigation */
  .nav-sidebar {
    padding-top: 40px;
    margin-right: -21px; /* 20px padding + 1px border */
    margin-bottom: 10px;
    margin-left: -20px;
  }
  .nav-sidebar > li > a {
    padding-right: 20px;
    padding-left: 20px;
    color: #595758;
    font-size: 14px;
  }
  
  .nav-sidebar > .active > a,
  .nav-sidebar > .active > a:hover,
  .nav-sidebar > .active > a:focus {
    color: rgba(255, 255, 255, 0.876);
    background-color: #de0d80 ;
  }

  .main {
    padding: 20px;
  }
  @media (min-width: 768px) {
    .main {
      padding-right: 40px;
      padding-left: 40px;
    }
  }
  .main .page-header {
    margin-top: 0;
    color: #ffd000;
    font-weight: bolder;
    background-color: #080808;
    height: 60px;
    font-size: 60px;
    
  }
  
  
  .main .overview-table {
    max-height: 100%;
    overflow-y: auto;
    margin-bottom: 15px;
  }
  
  .main .overview-table .table {
    padding-bottom: 0px;
    margin-bottom: 0px;
  }
